<template>
  <div>
    <!--  -->
    <el-page-header @back="goBack" :content="id?'':''"></el-page-header>
    <!--  -->
    <el-steps :active="active" simple>
      <el-step title="" icon="el-icon-edit"></el-step>
      <el-step title="" icon="el-icon-upload"></el-step>
      <el-step title="" icon="el-icon-picture"></el-step>
    </el-steps>
    <!--  -->
    <el-card class="card">
      <el-row>
        <el-tabs tab-position="left" :before-leave="levavetab">
          <!--  -->
          <el-tab-pane label="">
            <el-form
              :model="program.data"
              :rules="rulesadd"
              ref="addform"
              label-width="80px"
              class="demo-ruleForm"
              size="small"
            >
              <el-form-item label="" prop="title">
                <el-input v-model="program.data.title" class="editinp"></el-input>
              </el-form-item>
              <el-form-item label="" prop="subtitle">
                <el-input :placeholder="program.data.subtitle" v-model="program.data.subtitle"></el-input>
              </el-form-item>
              <el-form-item label="" prop="director">
                <el-input v-model="program.data.director" class="editinp"></el-input>
              </el-form-item>
              <el-form-item label="" prop="writer">
                <el-input v-model="program.data.writer" class="editinp"></el-input>
              </el-form-item>
              <el-form-item label="" prop="star">
                <el-input v-model="program.data.star" class="editinp"></el-input>
              </el-form-item>
              <el-form-item label="" prop="type">
                <el-input v-model="program.data.type" class="editinp"></el-input>
              </el-form-item>
              <el-form-item label="" prop="area">
                <el-input v-model="program.data.area" class="editinp"></el-input>
              </el-form-item>
              <el-form-item label="" prop="language">
                <el-input v-model="program.data.language" class="editinp"></el-input>
              </el-form-item>
              <el-form-item label="" prop="time">
                <el-input v-model="program.data.time" class="editinp"></el-input>
              </el-form-item>
              <el-form-item label="" prop="date">
                <el-input v-model="program.data.date" class="editinp"></el-input>
              </el-form-item>
              <el-form-item label="" prop="cover" class="cover">
                <div class="contain-cover">
                  <img
                    :src="id?'http://127.0.0.1:3000'+program.data.cover:program.data.cover"
                    :alt="program.data.title"
                    v-if="program.data.cover"
                    class="img-cover"
                  />
                  <div class="filebtn">
                    <i class="el-icon-plus"></i>
                    <input
                      type="file"
                      @change="uploadscover"
                      name="files"
                      ref="fileCover"
                      class="file"
                    />
                  </div>
                </div>
                <el-input v-model="program.data.cover" class="cover-input"></el-input>
              </el-form-item>
              <el-form-item label="banner" prop="banner">
                <div class="contain-cover">
                  <img
                    :src="id?'http://127.0.0.1:3000'+program.data.banner:program.data.banner"
                    v-if="program.data.banner"
                    class="img-cover img-banner"
                  />
                  <div class="filebtn">
                    <i class="el-icon-plus"></i>
                    <input type="file" @change="uploadsbanner" name="files" class="file" />
                  </div>
                </div>
                <el-input
                  :placeholder="program.data.banner"
                  v-model="program.data.banner"
                  class="cover-input"
                ></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input
                  type="textarea"
                  :placeholder="program.data.bio"
                  v-model="program.data.bio"
                ></el-input>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="">
            <el-button type="primary" size="small" @click="addpro" class="addpro" v-if="!id"></el-button>
            <el-form label-width="55px" size="mini" ref="addpro" class="form-up">
              <el-form-item
                :label="index+1+''"
                prop="title"
                v-for="(item,index) in title"
                :key="index"
              >
                <el-input v-model="Src[index].src" class="editinp-src"></el-input>
                <el-button
                  style="margin-left:20px;"
                  type="primary"
                  size="mini"
                  @click="updatepro(Src[index]._id,Src[index].src)"
                  v-if="id"
                ></el-button>
                <el-button
                  style="margin-left:20px;"
                  type="danger"
                  size="mini"
                  @click="deletepro(index,Src[index]._id)"
                ></el-button>
              </el-form-item>
              <el-form-item label="" v-if="id">
                <el-input v-model="src" class="upinput"></el-input>
                <el-button type="primary" @click="editprogram"></el-button>
              </el-form-item>
              <el-form-item v-else>
                <el-button type="primary" @click="addprogram"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </el-row>
      <div class="express">
        <p>*</p>
        <p>*</p>
        <div class="cover-prompt">
          <span>*,banner</span>
        </div>
      </div>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      active: 1,
      // 
      program: {
        title: "",
        data: {
          bio: "",
          title: " (2020)",
          director: "",
          writer: "",
          star: " /  /  /  / ",
          type: ": /  / ",
          area: "",
          language: "",
          date: "2020-01-22",
          time: "45",
          cover: "",
          src: "",
          subtitle: "",
          banner: ""
        }
      },
      // 
      rulesadd: {
        title: [{ required: true, message: "", trigger: "blur" }],
        director: [{ required: true, message: "", trigger: "blur" }],
        writer: [{ required: true, message: "", trigger: "blur" }],
        star: [{ required: true, message: "", trigger: "blur" }],
        type: [{ required: true, message: "", trigger: "blur" }],
        area: [{ required: true, message: "", trigger: "blur" }],
        language: [{ required: true, message: "", trigger: "blur" }],
        date: [{ required: true, message: "", trigger: "blur" }],
        time: [
          { required: true, message: "", trigger: "blur" },
          { max: 11, message: " 11 ", trigger: "blur" }
        ],
        cover: [{ required: true, message: "", trigger: "blur" }],
        banner: [{ required: true, message: "", trigger: "blur" }],
        subtitle: [{ required: true, message: "", trigger: "blur" }]
      },
      // 
      Src: [
        {
          title: "",
          src:
            "//player.yapi.xyz/p2p.php?url=https://bilibili.com-l-163.com/20181004/9753_762d7aa7/index.m3u8"
        }
      ],
      // 
      src: "",
      type: "",
      // 
      queryinfo: {
        title: "",
        pagesize: 12,
        pagenum: 1,
        id: ""
      },
      file: null,
      filebanner: null
    }
  },
  created() {
    // 
    this.program.title = this.path
    this.queryinfo.title = this.path
    this.queryinfo.id = this.id
    // id
    if (this.id) {
      this.findById()
    }
  },
  methods: {
    //
    goBack() {
      this.$router.go(-1)
    },
    // 
    async addprogram() {
      let data
      const result = await this.uploads(this.filebanner)
      this.program.data.banner = result.msg
      // 
      const { data: src } = await this.$http.post("/home/video", {
        title: this.program.data.title,
        src: this.Src,
        type: this.program.title
      })

      // 
      if (src.code !== 1)
        return this.$message({
          showClose: true,
          message: src.msg,
          duration: 1500,
          type: "error"
        })
      const { data: res } = await this.$http.post("/home/program", this.program)
      data = res
      if (data.code !== 1)
        return this.$message({
          showClose: true,
          message: data.msg,
          duration: 1500,
          type: "error"
        })
      this.$message({
        showClose: true,
        message: data.msg,
        duration: 1500,
        type: "success"
      })
    },
    // tab
    levavetab(activeName) {
      if (activeName === "0") {
        return (this.active = 1)
      }
      if (this.$refs.addform) {
        // 
        this.$refs.addform.validate(async valid => {
          if (valid) {
            // 
            if (this.id && activeName == 1) {
              this.active = 2
              const { data: res } = await this.$http.put(
                "/home/program",
                this.program
              )

              if (res.code !== 1) {
                this.$message({
                  showClose: true,
                  message: res.msg,
                  duration: 1500,
                  type: "error"
                })
              }
              if (res.code === 1) {
                this.$message({
                  showClose: true,
                  message: res.msg,
                  duration: 1500,
                  type: "success"
                })
                this.findById()
              }
              this.active = 2
              return true
            }
            // banner
            this.active = 2
            const result = await this.uploads(this.file)
            this.program.data.cover = result.msg

            console.log(result.msg)
          }
        })
      }

      if (activeName === "1" && this.active !== 2) {
        this.$message.error("")
        return false
      }
      // console.log(activeName);
      if (activeName === "2" && this.active !== 3) {
        this.$message.error("")
        return false
      }
    },
    // addpro
    addpro() {
      this.Src.push({ src: "" })
      console.log(this.Src)
    },
    async deletepro(index, id) {
      // id
      if (this.id) {
        console.log(id)
        const { data: res } = await this.$http.delete("/home/video", {
          params: { id, title: this.program.data.title }
        })
        if (res.code !== 1) return this.$message.error("")
        this.$message.success(res.msg)
        return this.findById()
      }
      this.Src.splice(index, 1)
    },
    // id
    async findById() {
      // id
      const { data: res } = await this.$http.get("/home/program", {
        params: this.queryinfo
      })
      if (res.code !== 1)
        return this.$message.error("")
      //idsrc
      const { data: src } = await this.$http.get("/home/video", {
        params: { title: res.data[0].title }
      })
      this.Src = src.data
      console.log(this.Src)
      delete res.data[0].createtime
      this.program.data = res.data[0]
      console.log(this.program.data)
    },
    async editprogram() {
      if (this.src.trim() === "") {
        this.src = ""
        return this.$message.error("")
      }
      const { data: src } = await this.$http.put("/home/video", {
        title: this.program.data.title,
        type: this.path,
        Src: this.src
      })

      if (src.code !== 1) return this.$message.error(src.msg)
      this.findById()
      this.$message({
        showClose: true,
        message: src.msg,
        duration: 1500,
        type: "success"
      })
    },
    // updateByid _id
    async updatepro(id, src) {
      console.log(21)
      const { data: res } = await this.$http.put("/home/videoById", {
        title: this.program.data.title,
        id,
        src
      })
      console.log(res)
    },
    // 
    async uploads(file, cover) {
      //  FormData  
      let formData = new FormData()
      console.log(file)

      formData.append("program", file) // 'file'   input name 
      formData.append("title", this.path)
      formData.append("id", this.id)
      formData.append("cover", cover)
      // 
      const { data: res } = await this.$http.post(
        "/home/program/upload",
        formData
      )
      return res
    },
    // 
    async uploadscover(e) {
      this.file = e.target.files[0]
      if (this.id) {
        const result = await this.uploads(this.file, "cover")
        if (result.code !== 1) return this.$message.error("")
        return (this.program.data.cover = result.msg)
      }
      console.log(e.target.files)
      this.program.data.cover = this.getObjectUrl(this.file)
      console.log(this.program.data.cover)
    },
    // banner
    async uploadsbanner(e) {
      this.filebanner = e.target.files[0]
      if (this.id) {
        const data = await this.uploads(this.filebanner)
        return (this.program.data.banner = data.msg)
      }
      const banner = this.getObjectUrl(this.filebanner)
      this.program.data.banner = banner
      console.log(this.program.data.banner)
    },
    getObjectUrl(file) {
      let url = null
      if (window.createObjectURL != undefined) {
        // basic
        url = window.createObjectURL(file)
      } else if (window.webkitURL != undefined) {
        // webkit or chrome
        url = window.webkitURL.createObjectURL(file)
      } else if (window.URL != undefined) {
        // mozilla(firefox)
        url = window.URL.createObjectURL(file)
      }
      return url
    }
  },
  computed: {
    path() {
      return this.$route.query.title
    },
    title() {
      return this.Src
    },
    id() {
      return this.$route.query.id
    }
  }
}
</script>
<style lang="scss" scoped>
.el-steps {
  margin: 15px 0;
}
.el-form {
  width: 80%;
}
.addpro {
  margin: 0 0 20px 20px;
}
.el-row {
  width: 70%;
  margin: 0 auto;
}
.form-up {
  width: 85%;
}
.editinp-src {
  width: 50%;
}
.src-title {
  width: 50px;
  margin-right: 10px;
}
.upinput {
  width: 67%;
  margin-right: 20px;
}
.card {
  position: relative;
  min-height:260px;
}
.express {
  width: 260px;
  height: 250px;
  position: absolute;
  right: 50px;
  top: 50px;
  color: rgb(103, 211, 224);
  font-size: 13px;
  line-height: 24px;
}
// 
.file {
  position: absolute;
  z-index: 2;
  left: 0px;
  top: 0px;
  height: 148px;
  width: 148px;
  opacity: 0;
}
.cover {
  position: relative;
}
.contain-cover:hover .filebtn {
  border-color: #409eff;
}
.contain-cover {
  display: flex;
}
.contain-cover img {
  margin-right: 15px;
  background-size: cover;
  border-radius: 6px;
}
.filebtn {
  background-color: #fbfdff;
  border: 1px dashed #c0ccda;
  border-radius: 6px;
  box-sizing: border-box;
  width: 148px;
  height: 148px;
  cursor: pointer;
  font-size: 28px;
  color: #8c939d;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.img-cover {
  width: 148px;
  height: 148px;
}
.cover-prompt {
  margin-top: 10px;
  color: #f56c6c;
}
.img-banner {
  width: 296px;
  height: 148px;
}
.cover-input {
  position: absolute;
  width: 75%;
  opacity: 0;
}
</style>